  --primary: #4f46e5;
  --primary-light: #6366f1;
  --bg: #f9fafb;
  --bg-card: #fff;
  --text: #111827;
  --border: #e5e7eb;
}

body {
  margin: 0;
  font-family: system-ui, sans-serif;
  background: var(--bg);
  color: var(--text);
}

#app {
  display: flex;
  height: 100vh;
}

/* Sidebar */
aside {
  width: 250px;
  background: var(--bg-card);
  border-right: 1px solid var(--border);
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

aside h2 {
  margin-top: 0;
}

#project-list {
  list-style: none;
  padding: 0;
  flex: 1;
  overflow-y: auto;
}

#project-list li {
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#project-list li:hover {
  background: var(--border);
}

#project-list li.active {
  background: var(--primary);
  color: white;
}

#project-list li .project-name {
  flex-grow: 1;
  user-select: none;
}

#project-list li button.edit-project,
#project-list li button.delete-project {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  margin-left: 0.3rem;
  padding: 0 0.2rem;
  color: inherit;
}

#project-list li button.edit-project:hover {
  color: var(--primary-light);
}

#project-list li button.delete-project:hover {
  color: #b91c1c; /* red */
}

aside button {
  padding: 0.5rem;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

aside button:hover {
  background: var(--primary-light);
}

/* Main */
main {
  flex: 1;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#current-project-title {
  user-select: none;
}

#todo-list {
  list-style: none;
  padding: 0;
}

#todo-list li {
  background: var(--bg-card);
  margin: 0.5rem 0;
  padding: 0.75rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#todo-list li.completed {
  text-decoration: line-through;
  opacity: 0.6;
}

.todo-info {
  max-width: 75%;
}

.todo-title {
  font-weight: 600;
}

.todo-description {
  font-size: 0.9rem;
  color: #555;
  margin-top: 0.3rem;
  white-space: pre-wrap; /* preserve line breaks */
}

.badge {
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  font-size: 0.75rem;
  margin-left: 0.5rem;
  text-transform: capitalize;
  user-select: none;
}

.badge.low {
  background: #d1fae5;
  color: #065f46;
}

.badge.medium {
  background: #fef3c7;
  color: #92400e;
}

.badge.high {
  background: #fee2e2;
  color: #991b1b;
}

#todo-list small {
  margin-left: 0.5rem;
  color: gray;
  user-select: none;
}

.todo-actions {
  display: flex;
  gap: 0.4rem;
}

.todo-actions button {
  background: none;
  border: none;
  padding: 0.25rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  color: var(--text);
}

.todo-actions button:hover {
  background: var(--border);
}

/* Buttons */
button {
  font: inherit;
  cursor: pointer;
}

/* Modal */
#modal.hidden {
  display: none;
}

#modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: var(--bg-card);
  padding: 1.5rem;
  border-radius: 8px;
  width: 300px;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.5rem;
  margin-top: 1rem;
}

.modal-content input,
.modal-content textarea,
.modal-content select {
  width: 100%;
  padding: 0.4rem;
  margin-top: 0.2rem;
  margin-bottom: 0.8rem;
  border: 1px solid var(--border);
  border-radius: 4px;
}

/* Responsive */
@media (max-width: 768px) {
  #app {
    flex-direction: column;
  }

  aside {
    width: 100%;
    border-right: none;
    border-bottom: 1px solid var(--border);
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
  }

  #project-list {
    display: flex;
    flex-wrap: nowrap;
    padding: 0;
    margin: 0;
  }

  #project-list li {
    flex: 0 0 auto;
    margin-right: 0.5rem;
  }

  main {
    padding: 0.5rem;
  }

  header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  #todo-list li {
    flex-direction: column;
    align-items: flex-start;
  }

  #todo-list li > .todo-actions {
    margin-top: 0.5rem;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    gap: 0.5rem;
  }

  button {
    padding: 0.4rem 0.7rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 400px) {
  aside h2, main header h2 {
    font-size: 1.2rem;
  }

  #project-list li {
    padding: 0.25rem 0.5rem;
    font-size: 0.85rem;
  }

  #todo-list li {
    font-size: 0.9rem;
  }
}

body.dark {
  background: #121212;
  color: #f1f1f1;
}
body.dark button {
  background: #333;
  color: white;
}
